export default {
	name: 'MyTab',
	props: {
		items: {
			type: Array,
			default: () => {
				return []
			}
		},
		current: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			c: this.current
		}
	},
	watch: {
		current (newVal, oldVal) {
			this.c = newVal
		}
	},
	methods: {
		tabSelect (index) {
			this.c = index
			this.$emit('onTabSelect', index)
		}
	}
}
